.dark-mode{
    background-color: darken($grisOscuro, 10);

    p{
        color: $blanco;
    }

    a, h1, h2, h3, h4{
        color: $gris;
    }

    .anuncio{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        
        .precio{
            color: $verde;
        }

        .icono{
            filter: invert(100%);
        }
    }

    .texto-nosotros blockquote{
        color: $blanco;
    }

    .formulario{
        legend, p, label{
            color: $gris;
        }
        input:not([type="submit"]), textarea, select{
            background-color: $grisOscuro;
            color: $blanco;
        }
    }

    .resumen-propiedad{
        .icono{
            filter: invert(100%);
        }
        .precio{
            color: $verde;
        }
    }

    table.propiedades{
        td{
            color: $blanco;
        }
    }
}